import axios from "src/utils/axios";
import productTypeService from "src/services/productTypeService";
export function createProductTypeRequest(payload, onSuccess, onError) {
  return async (dispatch) => {
    try {
      const user = await productTypeService.create(payload);
      console.log("user", user);
      onSuccess(user);
    } catch (error) {
      onError(error);
      throw error;
    }
  };
}
export function getProductTypeRequest(onSuccess, onError) {
  return async (dispatch) => {
    try {
      const list = await productTypeService.getList();
      onSuccess(list.data);
    } catch (error) {
      onError(error);
      throw error;
    }
  };
}
export function getProductTypeDetailRequest(path, onSuccess, onError) {
  return async (dispatch) => {
    try {
      const list = await productTypeService.getDetail(path);
      onSuccess(list.data);
    } catch (error) {
      onError(error);
      throw error;
    }
  };
}
export function editProductTypeRequest(payload, onSuccess, onError) {
  return async (dispatch) => {
    try {
      const user = await productTypeService.editItem(payload);
      onSuccess(user);
    } catch (error) {
      onError(error);
      throw error;
    }
  };
}
export function deleteProductTypeRequest(payload, onSuccess, onError) {
  return async (dispatch) => {
    try {
      const user = await productTypeService.deleteItem(payload);
      onSuccess(user);
    } catch (error) {
      onError(error);
      throw error;
    }
  };
}
