const useStyles = makeStyles((theme) => ({
  root: {
    height: "100%",
  },
  browseButton: {
    marginLeft: theme.spacing(2),
  },
  intro: {
    // background: url("../img/intro-bg.jpg"),
    height: 902,
    backgroundSize: "cover",
    display: "flex",
    alignItems: "center",
  },
  ul: {
    "& .MuiPaginationItem-root": {
      color: "#DA9E26",
    },
  },
}));
const listNew = Array.from({ length: 10 }).map(
  (_, index) => `Slide ${index + 1}`
);

function ProjectsView() {
  const sortRef = useRef(null);
  const classes = useStyles();
  const dispatch = useDispatch();
  const [openSort, setOpenSort] = useState(false);
  const [selectedSort, setSelectedSort] = useState({ title: "Tất cả", id: -1 });

  const [listProject, setListProject] = useState([]);
  const [listArea, setTistArea] = useState([]);
  const [currentArea, setCurrentArea] = useState("");
  const [listProjectTop, setListProjectTop] = useState([]);
  const [listProjectBottom, setListProjectBottom] = useState([]);
  const [listProjectBottomFilter, setListProjectBottomFilter] = useState([]);
  const [listProjectHighlight, setListProjectHighlight] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const handleChange = (event, value) => {
    setCurrentPage(value);
  };
  const [mobileView, setMobileView] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const getListProject = useCallback(() => {
    dispatch(
      getListProjectAnotherRequest(
        (res) => {
          setListProjectBottom(res);
          setListProjectBottomFilter(res);
        },
        (error) => {}
      )
    );
  }, []);
  const getListProjectHighLight = useCallback(() => {
    dispatch(
      getListProjectHighLightRequest(
        (res) => {
          setListProjectHighlight(res);
        },
        (error) => {}
      )
    );
  }, []);
  const getListArea = useCallback(() => {
    dispatch(
      getAreaRequest(
        (res) => {
          let currentArea = res.filter((item) => item.active === 1)[0];
          setCurrentArea(currentArea);
        },
        (error) => {}
      )
    );
  }, []);
  const getListProjectArea = useCallback(() => {
    dispatch(
      getListProjectAreaRequest(
        (res) => {
          setListProjectTop(res);
        },
        (error) => {}
      )
    );
  }, []);
  useEffect(() => {
    getListProject();
    getListProjectArea();
    getListArea();
    getListProjectHighLight();
  }, []);
  const handleSortSelect = (value) => {
    setSelectedSort(value);
    if (value.id === "-1") {
      setListProjectBottomFilter(listProjectBottom);
    } else {
      let newList = listProjectBottom.filter(
        (item) => item.status === value.id
      );
      setListProjectBottomFilter(newList);
    }
    setOpenSort(false);
  };

  const handleSortOpen = () => {
    setOpenSort(true);
  };

  const handleSortClose = () => {
    setOpenSort(false);
  };
  useStyles();
  const history = useHistory();
  const handleClick = (item) => {
    history.push({
      pathname: "/du-an/" + item.pathSEO,
      state: item,
    });
  };
  const onClick = () => {
    return <Redirect to="/posts/" />;
  };

  return (
    <Page title="Dự án" className={classes.root}>
      <div
        className="container-ct"
        style={{ height: window.innerHeight, paddingTop: 88, paddingRight: 0 }}
      >
        <Swiper
          // navigation={{
          //   // Both prevEl & nextEl are null at render so this does not work
          //   prevEl: prevRef.current,
          //   nextEl: nextRef.current,
          // }}

          slidesPerView={!mobileView ? 3 : 1}
          // freeMode={true}
          spaceBetween={8}
          // autoplay={{
          //   delay: 5000,
          //   disableOnInteraction: true,
          // }}
          pagination={{
            clickable: true,
            type: null,
          }}
          // onInit={(swiper) => {
          //   swiper.params.navigation.prevEl = prevRef.current;
          //   swiper.params.navigation.nextEl = nextRef.current;
          //   swiper.navigation.init();
          //   swiper.navigation.update();
          // }}
          // className="mySwiper"
          // loop={true}
          onSlideChange={(swiper) => {
            //   setActiveIndex(swiper.activeIndex);
          }}
          activeIndex={0}
        >
          {listNew !== [] &&
            listNew.slice(0, 10).map((item, index) => {
              return (
                <SwiperSlide>
                  <div
                    style={{ height: window.innerHeight - 144 }}
                    className="slider-view hover-ct"
                    onClick={() => {
                      handleClick(item);
                    }}
                  >
                    <div
                      src={project1}
                      style={{
                        height: window.innerHeight - 144,
                        objectFit: "cover",
                        width: "100%",
                        backgroundImage: `url(${project1})`,
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                      }}
                    >
                      <div>
                        <div className="txt-title">Interior Design</div>
                        <div className="txt-content">Comercial</div>
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
              );
            })}
        </Swiper>
      </div>
    </Page>
  );
}

export default ProjectsView;
