import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core";
import Page from "src/components/Page";
import Hero from "./Hero";
import Features from "./Features";
import Testimonials from "./Testimonials";
import CTA from "./CTA";
import FAQS from "./FAQS";
import HeaderTop from "./HeaderTop";
import About from "./About";
import MyServices from "./MyServices";
import Highlights from "./Highlights";
import Partners from "./Partners";
import News from "./News";
import Contact from "./Contact";
import bghome from "src/assets/images/bghome.png";
import iconPlay from "src/assets/images/iconPlay.png";
import { useTranslation } from "react-i18next";
import { withTranslation } from "react-i18next";

import AOS from "aos";
import "./index.css";
const useStyles = makeStyles(() => ({
  root: {},
}));

function HomeView() {
  const classes = useStyles();
  useStyles();
  const { t, i18n } = useTranslation("common");
  const [mobileView, setMobileView] = useState(false);
  const [widthView, setWidthView] = useState(window.innerWidth);
  const [widthViewContent, setWidthViewContent] = useState(
    window.innerWidth >= 1820 ? 1664 : 1170
  );

  useEffect(() => {
    const setResponsiveness = () => {
      setWidthView(window.innerWidth);
      if (window.innerWidth >= 1820) {
        setWidthViewContent(1664);
      } else {
        setWidthViewContent(1170);
      }
      return window.innerWidth < 1024
        ? setMobileView(true)
        : setMobileView(false);
    };
    setResponsiveness();
    window.addEventListener("resize", () => setResponsiveness());
    return () => {
      window.removeEventListener("resize", () => setResponsiveness());
    };
  }, []);
  return (
    <Page className={classes.root} title="Trang chủ">
      {/* <Hero /> */}

      <div
        style={{
          height: window.innerHeight,
          paddingTop: 88,
          display: "flex",
          paddingLeft: (widthView - widthViewContent) / 2,
        }}
      >
        <div
          data-aos="zoom-in-up"
          style={{
            width: widthViewContent / 3,
            height: 100,
            height: "100%",
            display: "flex",
            justifyContent: "center",
            flexDirection: "column",
          }}
        >
          <div className="title">
            DREAM <br /> HOME
          </div>
          <div style={{ display: "flex", marginTop: 24 }}>
            <div className="line-left"></div>
            <div className="title-content">{t("Home.TitleIntro")}</div>
          </div>
          <div style={{ display: "flex", marginTop: 56, alignItems: "center" }}>
            <img className="icon-play" src={iconPlay} />
            <div className="text-contact ">{t("Home.TitleContact")}</div>
            <div className="box-price ">{t("Home.TitlePrice")}</div>
          </div>
        </div>
        <div style={{ flex: 1 }}>
          <img
            src={bghome}
            style={{ height: "100%", objectFit: "cover", width: "100%" }}
          />
        </div>
      </div>
    </Page>
  );
}

export default withTranslation()(HomeView);
