import React, { useEffect, useState, useCallback } from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import {
  Box,
  Button,
  Container,
  Typography,
  makeStyles,
} from "@material-ui/core";
import "./Highlights.css";
import "swiper/swiper.min.css";
// import "swiper/components/pagination/pagination.min.css";
// import "swiper/components/navigation/navigation.min.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "./News.css";
import arrow2 from "src/assets/images/arrow2.png";
import { getBlogRequest } from "src/actions/blogActions";
import { useDispatch } from "react-redux";
import moment from "moment";
import { useHistory } from "react-router-dom";
const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.default,
    paddingTop: 128,
    paddingBottom: 128,
  },
  browseButton: {
    marginLeft: theme.spacing(2),
  },
}));

function News({ className, mobileView, ...rest }) {
  const dispatch = useDispatch();
  const history = useHistory();
  const [listNew, setListNew] = useState([]);
  useEffect(() => {}, []);
  const getBlogRequestHanled = useCallback(() => {
    dispatch(
      getBlogRequest(
        (res) => {
          setListNew(res);
        },
        (error) => {}
      )
    );
  }, []);
  useEffect(() => {
    getBlogRequestHanled();
  }, []);
  const classes = useStyles();
  const [withView, setWithView] = useState(window.innerWidth);

  let appendNumber = 600;
  let prependNumber = 1;

  const handleClick = (item) => {
    history.push({
      pathname: "/tin-tuc/" + item.pathSEO,
      state: item,
    });
  };
  // Create array with 1000 slides
  const slides = Array.from({ length: 5 }).map(
    (_, index) => `Slide ${index + 1}`
  );

  return (
    <div
      data-aos="fade-up"
      data-aos-anchor-placement="center-bottom"
      style={{ marginTop: 120 }}
    >
      <div className="container">
        <div className="row-custom">
          <div className="title-home">Tin tức</div>
          <a href="/tin-tuc" style={{ marginLeft: "auto" }}>
            {!mobileView ? (
              <div className="home-box-detail " style={{ marginLeft: "auto" }}>
                <div style={{ flex: 1 }}>
                  <div className="home-box-title">Trang Tin Tức</div>
                </div>
                <img className="arrow-see-more" src={arrow2} />
              </div>
            ) : (
              <div>
                <span className={"about-box-title"}>Tất cả tin tức</span>
                <img
                  className="arrow-see-more"
                  style={{ marginRight: 0 }}
                  src={arrow2}
                />
              </div>
            )}
          </a>
        </div>
      </div>
      <div
        className={"container-new"}
        style={{
          marginTop: 40,
          marginLeft:
            withView > 1200
              ? (withView - 1140) / 2
              : withView > 970
              ? (withView - 970) / 2
              : withView > 768
              ? (withView - 750) / 2
              : 0,
        }}
      >
        <Swiper
          // navigation={{
          //   // Both prevEl & nextEl are null at render so this does not work
          //   prevEl: prevRef.current,
          //   nextEl: nextRef.current,
          // }}
          freeMode={true}
          slidesPerView={!mobileView ? 3.33333 : 1.2}
          // freeMode={true}
          spaceBetween={0}
          autoplay={{
            delay: 5000,
            disableOnInteraction: true,
          }}
          pagination={{
            clickable: true,
            type: null,
          }}
          // onInit={(swiper) => {
          //   swiper.params.navigation.prevEl = prevRef.current;
          //   swiper.params.navigation.nextEl = nextRef.current;
          //   swiper.navigation.init();
          //   swiper.navigation.update();
          // }}
          // className="mySwiper"
          // loop={true}
          onSlideChange={(swiper) => {
            //   setActiveIndex(swiper.activeIndex);
          }}
          activeIndex={0}
        >
          {listNew !== [] &&
            listNew.slice(0, 10).map((item, index) => {
              return (
                <SwiperSlide>
                  <div
                    className="slider-view news-box hover-ct"
                    style={{
                      backgroundSize: "cover",
                      height: 306,
                      marginLeft: 16,
                      borderRadius: 8,
                      marginRight: 16,
                    }}
                    onClick={() => {
                      handleClick(item);
                    }}
                  >
                    <div
                      style={{
                        borderTopRightRadius: 8,
                        borderTopLeftRadius: 8,
                        height: 200,
                        backgroundColor: "#9797",
                        backgroundImage: `url(${item.thumbnail})`,
                        backgroundSize: "cover",
                      }}
                    ></div>
                    <div className="new-item">
                      <div className="title-new">{item.title}</div>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          marginTop: 8,
                        }}
                      >
                        <div className="item-time">
                          {moment(Number.parseFloat(item.datePosted)).format(
                            "DD/MM/YYY"
                          )}
                        </div>
                        <div
                          className="item-see-now"
                          style={{ marginLeft: "auto" }}
                        >
                          Xem ngay
                        </div>
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
              );
            })}
        </Swiper>
      </div>
    </div>
  );
}

News.propTypes = {
  className: PropTypes.string,
};

export default News;
