import React, { useEffect, useState } from "react";
import { Link as RouterLink } from "react-router-dom";
import clsx from "clsx";
import PropTypes from "prop-types";
import { AppBar, Box, Toolbar, Link, makeStyles } from "@material-ui/core";
import "./TopBar.css";
import logoNSG from "../../assets/images/logo.png";
import icon_search from "src/assets/images/icon_search.png";
import { useHistory } from "react-router-dom";

import { useTranslation, Trans } from "react-i18next";
import i18next from "i18next";
const useStyles = makeStyles((theme) => ({
  root: {
    // backgroundColor: theme.palette.background.default,
    // backgroundColor: "rgba(255,255,255,0.5)",
    backgroundColor: "#26282B",
    boxShadow: "0px 2px 4px rgba(34, 34, 34, 0.8)",
  },
  toolbar: {
    height: 88,
  },
  logo: {
    marginRight: theme.spacing(2),
  },
  link: {
    fontWeight: theme.typography.fontWeightMedium,
    "& + &": {
      marginLeft: theme.spacing(2),
    },
  },
  divider: {
    width: 1,
    height: 32,
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
  },
}));

function TopBar({ className, headersData, ...rest }) {
  const { header, logo, menuButton, toolbar, drawerContainer } = useStyles();
  const history = useHistory();
  const { t, i18n } = useTranslation("common");
  const classes = useStyles();
  const [state, setState] = useState({
    mobileView: false,
    drawerOpen: false,
    currentPath: "",
  });
  const [title, setTitle] = useState("");
  const [headerChecked, setHeaderChecked] = useState(false);
  const getLanguage = () => i18next.language || window.localStorage.i18nextLng;

  const { mobileView, drawerOpen } = state;
  useEffect(() => {
    const setResponsiveness = () => {
      return window.innerWidth < 1024
        ? setState((prevState) => ({ ...prevState, mobileView: true }))
        : setState((prevState) => ({ ...prevState, mobileView: false }));
    };
    setResponsiveness();
    window.addEventListener("resize", () => setResponsiveness());
    return () => {
      window.removeEventListener("resize", () => setResponsiveness());
    };
  }, []);
  const getTitle = (pathName) => {
    headersData.forEach((element) => {
      if (element.href.indexOf(pathName) > -1) {
        return setTitle(element.label);
      }
    });
  };
  useEffect(() => {
    let pathname = window.location.pathname;
    if (window.location.pathname === "/") {
      pathname = "/home";
    }
    if (pathname !== "") {
      getTitle(pathname);
    }
    setState((prevState) => ({
      ...prevState,
      currentPath: pathname,
    }));
  }, [window.location.pathname]);
  const onClickLink = (item) => {
    let check = document.getElementById("navi-toggle");
    if (check) check.checked = false;
    setHeaderChecked(false);
    history.push(item.href);
  };
  const onChangeHandle = (event) => {
    console.log(event.target.checked);
    setHeaderChecked(event.target.checked);
  };
  const displayMobile = () => {
    return (
      <div class="navigation">
        <input
          type="checkbox"
          class="navigation__checkbox"
          id="navi-toggle"
          onChange={onChangeHandle}
        />
        <label for="navi-toggle" class="navigation__button">
          <span class="navigation__icon"></span>
        </label>
        <div class="navigation__background">&nbsp;</div>

        <nav class="navigation__nav">
          {headerChecked === true && (
            <ul class="navigation__list">
              {headersData.map((item) => {
                return (
                  <li class="navigation__item">
                    <div
                      class="navigation__link"
                      onClick={() => {
                        onClickLink(item);
                      }}
                    >
                      {item.label}
                    </div>
                  </li>
                );
              })}
            </ul>
          )}
        </nav>
      </div>
    );
  };
  return (
    <AppBar className={clsx(classes.root, className)} color="default" {...rest}>
      {!mobileView ? (
        <Toolbar
          className={"container-ct"}
          style={{ paddingLeft: 0, paddingRight: 0 }}
        >
          <div className="col-md-4">
            <img
              style={{
                width: 70,
                height: 64,
              }}
              src={logoNSG}
            />
          </div>
          <div style={{ flex: 1, flexDirection: "row", display: "flex" }}>
            {headersData.map((item) => {
              let check =
                state.currentPath.indexOf(item.href) > -1 ? true : false;
              return (
                <div
                  className={check ? "view-link-item active" : "view-link-item"}
                >
                  <Link
                    className={check ? "title-active link-item " : "link-item "}
                    component={RouterLink}
                    to={item.href}
                  >
                    {t("TopBar." + item.label)}
                  </Link>
                </div>
              );
            })}
          </div>
          <div style={{ display: "flex", color: "#fff", marginTop: 15 }}>
            <img className="icon-search" src={icon_search} />
            <div
              className={
                getLanguage() === "vi" ? "link-item title-active " : "link-item"
              }
              onClick={() => {
                i18n.changeLanguage("vi");
                localStorage.setItem("currentLanguage", "vi");
                window.location.reload();
              }}
            >
              Vn&nbsp;
            </div>
            <div>/ </div>
            <div
              className={
                getLanguage() === "en" ? "link-item title-active " : "link-item"
              }
              onClick={() => {
                i18n.changeLanguage("en");
                localStorage.setItem("currentLanguage", "en");
                window.location.reload();
              }}
            >
              &nbsp;En
            </div>
          </div>
        </Toolbar>
      ) : (
        <div style={{ backgroundColor: "#fff", height: 48 }}>
          <img
            style={{
              width: 53,
              height: 64,
              // position: "absolute",
              // top: 14,
              // marginLeft: 24,
              zIndex: 100,
            }}
            src={logoNSG}
          />
          <div
            className="title-pathName"
            style={{ textAlign: "center", marginTop: 14 }}
          >
            {title}
          </div>
          {displayMobile()}
        </div>
      )}
    </AppBar>
  );
}

TopBar.propTypes = {
  className: PropTypes.string,
};

export default TopBar;
