import jwtDecode from "jwt-decode";
import axios from "src/utils/axios";

class ProductTypeService {
  create = (payload) =>
    new Promise((resolve, reject) => {
      console.log("payload", payload);
      axios
        .post("/producttype/create", payload)
        .then((response) => {
          console.log("response", response);
          if (response.data.message === 1) {
            resolve(response.data.post);
          } else {
            reject(response.data.error);
          }
        })
        .catch((error) => {
          reject(error);
        });
    });
  getList = () =>
    new Promise((resolve, reject) => {
      axios
        .get("/producttype/all")
        .then((response) => {
          if (response.data.message === 1) {
            resolve(response.data);
          } else {
            reject(response.data.error);
          }
        })
        .catch((error) => {
          reject(error);
        });
    });
  getDetail = (path) =>
    new Promise((resolve, reject) => {
      axios
        .get("/producttype/detail/" + path)
        .then((response) => {
          if (response.data.message === 1) {
            resolve(response.data);
          } else {
            reject(response.data.error);
          }
        })
        .catch((error) => {
          reject(error);
        });
    });

  editItem = (payload) =>
    new Promise((resolve, reject) => {
      axios
        .put("/producttype/edit?itemID=" + payload.id, payload)
        .then((response) => {
          console.log("response", response);
          if (response.data.message === 1) {
            resolve(response.data);
          } else {
            reject(response.data.error);
          }
        })
        .catch((error) => {
          reject(error);
        });
    });
  deleteItem = (payload) =>
    new Promise((resolve, reject) => {
      axios
        .delete("/producttype/delete?itemID=" + payload.id)
        .then((response) => {
          console.log("response", response);
          if (response.data.message === 1) {
            resolve(response.data);
          } else {
            reject(response.data.error);
          }
        })
        .catch((error) => {
          reject(error);
        });
    });
}

const productTypeService = new ProductTypeService();

export default productTypeService;
